.header {
  position: relative;
  z-index: 100;
  padding: 30px 0;
  @media (max-width: 991px) {
    position: absolute;
    left: 0;
    right: 0;
  }
  &__container {
    display: flex;
    justify-content: space-between;
  }
  &__logo {
    display: inline-block;
    width: 175px;
    height: 100px;
    a {
      color: white;
      font-size: 50px;
      line-height: 44.75px;
      @media (max-width: 991px) {
        font-size: 43px;
        line-height: 43px;
      }
      @media (max-width: 767px) {
        font-size: 37px;
        line-height: 37px;
      }
      @media (max-width: 575px) {
        font-size: 30px;
        line-height: 30.75px;
      }
      &:hover {
        text-decoration: none;
      }
    }
  }
  &__menu {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    @media (max-width: 991px) {
      display: none;
    }
    ul {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      list-style: none;
      li {
        margin-left: 26px;
        &:first-child {
          margin-left: 0;
        }
      }
      a {
        position: relative;
        color: #d8d8d8;
        font-size: 25px;
        transition: 0.2s;
        &:before {
          content: "";
          position: absolute;
          width: 0;
          transition: 0.3s;
          height: 2px;
          background-color: white;
          bottom: -6px;
          left: 0;
          right: 0;
          @media (max-width: 767px) {
            content: none;
          }
        }
        &:hover {
          text-decoration: none !important;
          color: white;
          &:before {
            width: 100%;
          }
        }
      }
    }
  }
  &__mobile {
    display: inline-block;
    cursor: pointer;
    @media (min-width: 992px) {
      display: none;
    }
  }
  &__mobile-menu {
    position: fixed;
    visibility: hidden;
    background-color: #038697;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    right: -100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
    z-index: 300;
    padding-left: 0;
    ul {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      list-style: none;
      margin-top: -50px;
      padding-left: 0;
      li {
        margin-top: 26px;
        text-align: center;
        &:first-child {
          margin-top: 0;
        }
      }
      a {
        color: #d8d8d8;
        font-size: 25px;
        transition: 0.2s;
        &:hover {
          color: white;
          text-decoration: underline;
        }
      }
    }
    @media (min-width: 992px) {
      display: none;
    }
    &__active {
      right: 0;
      visibility: visible;
    }
    .header__menu {
      flex-direction: column;
    }
  }
  &__close-menu {
    cursor: pointer;
    position: absolute;
    top: 30px;
    right: 20px;
    width: 25px;
  }
}



