.about-descr {
  padding: 165px 15px;
  @media (max-width: 991px) {
    padding: 120px 15px;
  }
  @media (max-width: 767px) {
    padding: 80px 15px;
  }
  @media (max-width: 575px) {
    padding: 60px 15px;
  }
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 575px) {
      flex-direction: column;
    }
  }
  &__text {
    max-width: 600px;
    width: 100%;
    p {
      color: #414141;
      font-size: 35px;
      line-height: 40px;
      @media (max-width: 1200px) {
        font-size: 30px;
        line-height: 30px;
      }
      @media (max-width: 991px) {
        font-size: 25px;
        line-height: 25px;
      }
      @media (max-width: 767px) {
        font-size: 22px;
        line-height: 22px;
      }
      @media (max-width: 575px) {
        font-size: 18px;
        line-height: 18px;
      }
    }
  }
  &__img {
    img {
      max-width: 100%;
      @media (max-width: 575px) {
        max-width: 350px;
        width: 100%;
        margin-top: 30px;
      }
    }
  }
}
.work {
  position: relative;
  &__container {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
  }
  &__left {
    width: 50%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    background-size: cover;
    background-repeat: no-repeat;
    @media (max-width: 767px) {
      position: relative;
      height: 600px;
      width: 100%;
      background-position: center;
    }
    @media (max-width: 575px) {
      height: 500px;
    }
  }
  &__right {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    @media (max-width: 767px) {
      width: 100%;
    }
  }
  &__content {
    padding-top: 85px;
    padding-bottom: 110px;
    width: 50%;
    padding-left: 60px;
    @media (max-width: 1200px) {
      padding-left: 30px;
      padding-top: 75px;
      padding-bottom: 70px;
    }
    @media (max-width: 767px) {
      padding-left: 0;
      width: 100%;
      padding-top: 70px;
      padding-bottom: 50px;
    }
    @media (max-width: 575px) {
      padding-top: 60px;
      padding-bottom: 20px;
    }
    h2 {
      color: #a20061;
      font-size: 80px;
      margin-bottom: 85px;
      @media (max-width: 991px) {
        font-size: 65px;
        margin-bottom: 70px;
      }
      @media (max-width: 767px) {
        font-size: 52px;
        margin-bottom: 60px;
      }
      @media (max-width: 575px) {
        font-size: 45px;
        margin-bottom: 50px;
      }
    }
    p {
      color: #666;
      font-size: 40px;
      line-height: 52.75px;
      margin-bottom: 60px;
      @media (max-width: 991px) {
        font-size: 35px;
      }
      @media (max-width: 767px) {
        font-size: 30px;
      }
      @media (max-width: 575px) {
        font-size: 25px;
      }
    }
    a {
      color: #666;
      font-size: 50px;
      line-height: 52.75px;
      text-decoration: none;
      margin-bottom: 60px;
      display: inline-block;
      position: relative;
      transition: 0.2s;
      @media (max-width: 1350px) {
        font-size: 39px;
        line-height: 39px;
        margin-bottom: 50px;
      }
      @media (max-width: 991px) {
        font-size: 35px;
        line-height: 35px;
        margin-bottom: 45px;
        display: block;
      }
      @media (max-width: 767px) {
        font-size: 30px;
        line-height: 30px;
        margin-bottom: 40px;
      }
      @media (max-width: 575px) {
        font-size: 27px;
        line-height: 27px;
        margin-bottom: 35px;
      }
      &:before {
        content: "";
        position: absolute;
        width: 0;
        transition: 0.3s;
        height: 3px;
        background-color: #038597;
        bottom: 3px;
        left: 0;
        right: 0;
        @media (max-width: 767px) {
          content: none;
        }
      }
      &:hover {
        text-decoration: none !important;
        color: #038597;
        &:before {
          width: 100%;
        }
        @media (max-width: 991px) {
          text-decoration: underline;
          &:before {
            width: 0;
          }
        }
      }
    }
  }
}
.about {
  .top-hero {
    &__heading-form {
      @media (max-height: 575px) {
        justify-content: center;
      }
    }
  }
}



