.top-descr {
  background-color: #038697;
  padding-top: 110px;
  padding-bottom: 138px;
  @media (max-width: 991px) {
    padding-top: 80px;
    padding-bottom: 100px;
  }
  @media (max-width: 767px) {
    padding-top: 50px;
    padding-bottom: 70px;
  }
  @media (max-width: 575px) {
    padding-top: 30px;
    padding-bottom: 40px;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    @media (max-width: 767px) {
      flex-direction: column;
    }
  }
  &__column {
    max-width: 381px;
    @media (max-width: 1200px) {
      padding-right: 15px;
      &:last-child {
        padding-right: 0;
      }
    }
    @media (max-width: 767px) {
      margin-top: 30px;
      padding-right: 0;
      margin-left: auto;
      margin-right: auto;
    }
    h3 {
      color: white;
      font-size: 35px;
      margin-bottom: 16px;
      @media (max-width: 991px) {
        font-size: 32px;
      }
      @media (max-width: 767px) {
        font-size: 30px;
      }
      @media (max-width: 575px) {
        font-size: 27px;
      }
    }
    p {
      font-size: 25px;
      color: white;
      @media (max-width: 991px) {
        font-size: 22px;
      }
      @media (max-width: 767px) {
        font-size: 20px;
      }
      @media (max-width: 575px) {
        font-size: 18px;
      }
    }
  }
}
.landlord {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  position: relative;
  @media (max-width: 767px) {
    flex-direction: column;
  }
  &__left {
    width: 100%;
  }
  &__content {
    width: 50%;
    padding-right: 60px;
    @media (max-width: 767px) {
      width: 100%;
    }
    h2 {
      color: #191919;
      font-size: 80px;
      line-height: 105.5px;
      margin-bottom: 40px;
      margin-top: 90px;
      @media (max-width: 991px) {
        font-size: 64px;
        line-height: 80px;
      }
      @media (max-width: 767px) {
        font-size: 52px;
        line-height: 65px;
      }
      @media (max-width: 575px) {
        font-size: 45px;
        line-height: 50px;
      }
    }
    p {
      color: #414141;
      font-size: 32px;
      line-height: 45.6px;
      @media (max-width: 991px) {
        font-size: 28px;
        line-height: 35px;
      }
      @media (max-width: 767px) {
        font-size: 22px;
        line-height: 22px;
      }
      @media (max-width: 575px) {
        font-size: 18px;
        line-height: 18px;
      }
    }
    h3 {
      color: #038597;
      font-size: 40px;
      line-height: 51.3px;
      margin-bottom: 0;
      margin-top: 65px;
      @media (max-width: 991px) {
        margin-top: 55px;
        font-size: 35px;
        line-height: 45px;
      }
      @media (max-width: 767px) {
        margin-top: 42px;
        font-size: 30px;
        line-height: 37px;
      }
      @media (max-width: 575px) {
        margin-top: 30px;
        font-size: 25px;
        line-height: 30px;
      }
      &:first-child {
        margin-top: 100px;
      }
    }
    .offer-button {
      height: 100px;
      border-radius: 10px;
      background-color: #0099a2;
      padding: 0 52px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      color: white;
      font-size: 50px;
      max-width: 370px;
      margin-top: 68px;
      margin-bottom: 100px;
      transition: 0.1s;
      &:hover {
        text-decoration: none;
        transform: scale(0.95);
      }
      @media (max-width: 991px) {
        height: 80px;
        padding: 0 42px;
        font-size: 35px;
        max-width: 320px;
        margin-bottom: 80px;
      }
      @media (max-width: 767px) {
        height: 65px;
        padding: 0 37px;
        font-size: 30px;
        max-width: 250px;
        margin-bottom: 65px;
      }
      @media (max-width: 575px) {
        height: 50px;
        padding: 0 20px;
        font-size: 27px;
        max-width: 200px;
        margin-bottom: 50px;
      }
    }
  }
  &__right {
    width: 50%;
    height: 100%;
    right: 0;
    position: absolute;
    background-size: cover;
    @media (max-width: 767px) {
      width: 100%;
      position: relative;
      max-height: 500px;
      height: 500px;
      background-position-y: center;
    }
  }
}



