.footer {
  background-color: #038697;
  color: white;
  padding-top: 130px;
  padding-bottom: 50px;
  @media (max-width: 991px) {
    padding-top: 90px;
    padding-bottom: 40px;
  }
  @media (max-width: 767px) {
    padding-top: 55px;
    padding-bottom: 35px;
  }
  @media (max-width: 575px) {
    padding-top: 20px;
    padding-bottom: 30px;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    @media (max-width: 767px) {
      flex-direction: column;
      text-align: center;
    }
  }
  &__contact {
    max-width: 300px;
    @media (max-width: 767px) {
      max-width: 100%;
    }
    h3 {
      font-size: 28px;
      line-height: 31.92px;
      margin-top: 45px;
      @media (max-width: 991px) {
        font-size: 26px;
        line-height: 28px;
        margin-top: 40px;
      }
      @media (max-width: 767px) {
        font-size: 24px;
        line-height: 28px;
        margin-top: 38px;
      }
      @media (max-width: 575px) {
        font-size: 22px;
        line-height: 28px;
        margin-top: 35px;
      }
    }
    p {
      color: #d9d5d5;
      font-size: 25px;
      line-height: 28.5px;
      @media (max-width: 991px) {
        font-size: 22px;
        line-height: 22px;
      }
      @media (max-width: 575px) {
        font-size: 20px;
        line-height: 20px;
      }
    }
    a {
      color: #d9d5d5;
      font-size: 25px;
      line-height: 28.5px;
      transition: 0.2s;
      &:hover {
        cursor: pointer;
        text-decoration: none;
        color: white;
      }
      @media (max-width: 991px) {
        font-size: 22px;
        line-height: 22px;
      }
      @media (max-width: 575px) {
        font-size: 20px;
        line-height: 20px;
      }
    }
  }
  &__menu {
    display: flex;
    align-items: center;
    margin-top: 45px;
    @media (max-width: 991px) {
      margin-top: 40px;
    }
    @media (max-width: 767px) {
      margin-top: 35px;
      margin-bottom: 10px;
      justify-content: center;
      ul {
        padding-left: 0;
        li {
          text-align: center;
        }
      }
    }
    ul {
      list-style-type: none;
      text-align: right;
    }
    a {
      font-size: 45px;
      color: #d8d8d8;
      transition: 0.3s;
      position: relative;
      &:before {
        content: "";
        position: absolute;
        width: 0;
        transition: 0.3s;
        height: 2px;
        background-color: white;
        bottom: -6px;
        left: 0;
        right: 0;
        @media (max-width: 767px) {
          content: none;
        }
      }
      &:hover {
        text-decoration: none !important;
        color: white;
        &:before {
          width: 100%;
        }
      }
      @media (max-width: 991px) {
        font-size: 38px;
      }
      @media (max-width: 767px) {
        font-size: 31px;
      }
      @media (max-width: 575px) {
        font-size: 27px;
      }
    }
  }
  &-bottom__container {
    position: relative;
    margin-top: 100px;
    @media (max-width: 991px) {
      margin-top: 80px;
    }
    @media (max-width: 767px) {
      text-align: center;
      margin-top: 10px;
    }
  }
  &__logo {
    display: inline-block;
    width: 175px;
    height: 100px;
    color: white;
    font-size: 50px;
    line-height: 44.75px;
    @media (max-width: 991px) {
      font-size: 40px;
      line-height: 40px;
      height: 87px;
    }
    @media (max-width: 767px) {
      font-size: 35px;
      line-height: 35px;
      height: 75px;
    }
    @media (max-width: 575px) {
      font-size: 30px;
      line-height: 30px;
      height: 65px;
    }
    &:hover {
      text-decoration: none;
      color: white;
    }
  }
  &__copyright {
    color: #c0c0c0;
    font-size: 25px;
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: 0;
    width: 405px;
    @media (max-width: 991px) {
      font-size: 23px;
    }
    @media (max-width: 767px) {
      position: relative;
      width: 100%;
      font-size: 20px;
    }
    @media (max-width: 575px) {
      font-size: 18px;
    }
  }
}



