.top-hero {
  height: 100vh;
  min-height: 375px;
  max-height: 880px;
  position: relative;
  &__green-image {
    background-position: bottom;
    background-size: cover;
    height: 100%;
  }
  &__mask {
    background-color: rgba(0, 153, 162, 0.7);
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
  }
  &__heading-form {
    @media (max-width: 991px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 100%;
      justify-content: center;
    }
    @media (max-height: 575px) {
      justify-content: flex-end;
    }
  }
  &__heading {
    position: relative;
    margin-top: 60px;
    margin-bottom: 50px;
    max-width: 1200px;
    @media (max-width: 991px) {
      margin-top: 0;
      margin-bottom: 40px;
    }
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
    @media (max-width: 575px) {
      margin-bottom: 15px;
    }
    @media (max-height: 575px) {
      margin-bottom: 15px;
    }
    @media (max-height: 420px) {
      margin-bottom: 0;
    }
    h1 {
      color: white;
      font-size: 100px;
      line-height: 89.5px;
      @media (max-width: 991px) {
        font-size: 80px;
        line-height: 80px;
        text-align: center;
      }
      @media (max-width: 767px) {
        font-size: 65px;
        line-height: 65px;
      }
      @media (max-width: 575px) {
        font-size: 50px;
        line-height: 50px;
      }
    }
    h2 {
      color: #dfdfdf;
      font-size: 75px;
      line-height: 72.75px;
      @media (max-width: 991px) {
        font-size: 65px;
        line-height: 65px;
        text-align: center;
      }
      @media (max-width: 767px) {
        font-size: 50px;
        line-height: 50px;
      }
      @media (max-width: 575px) {
        font-size: 40px;
        line-height: 40px;
      }
    }
  }
  &__form {
    position: relative;
    form {
      margin-bottom: 0;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      max-width: 1165px;
      margin-left: auto;
      margin-right: auto;
      @media (max-width: 991px) {
        flex-wrap: wrap;
        justify-content: center;
      }
    }
  }
  .select2 {
    width: 100% !important;
    max-width: 713px;
    height: 75px !important;
    font-size: 35px;
    border-radius: 0;
    outline: none;
    @media (max-width: 991px) {
      font-size: 30px;
      max-width: 100%;
      height: 60px !important;
    }
    @media (max-width: 575px) {
      font-size: 25px;
    }
    .select2-selection {
      height: 75px;
      display: flex;
      flex-direction: row;
      align-items: center;
      border-radius: 0;
      box-shadow: none;
      outline: none;
      border: none;
      @media (max-width: 991px) {
        height: 60px !important;
      }
      @media (max-width: 575px) {
        height: 50px !important;
      }
    }
    .select2-search--dropdown {
      outline: none;
    }
    .select2-selection__arrow {
      background: url("images/arrow-down.svg");
      width: 32px;
      height: 24px;
      background-repeat: no-repeat;
      top: auto;
      right: 22px;
      transition: 0.3s;
      b {
        display: none;
      }
    }
    .select2-selection__rendered {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-left: 22px;
      @media (max-width: 991px) {
        margin-left: 13px;
      }
      @media (max-width: 575px) {
        margin-left: 5px;
      }
      &:before {
        content: url("images/location.svg");
        padding-right: 38px;
        @media (max-width: 991px) {
          padding-right: 23px;
        }
        @media (max-width: 575px) {
          padding-right: 8px;
        }
      }
    }
  }
  .select2-container--open {
    .select2-selection__arrow {
      transform: rotate(180deg);
      margin-bottom: -2px;
      margin-top: -4px;
    }
  }
  &__adress {
    width: 100%;
    max-width: 713px;
    height: 75px;
    font-size: 35px;
    border-radius: 0;
    outline: none;
    position: relative;
    @media (max-width: 991px) {
      font-size: 30px;
      max-width: 100%;
      height: 60px !important;
    }
    @media (max-width: 575px) {
      font-size: 25px;
    }
    input {
      width: 100%;
      max-width: 713px;
      height: 75px;
      font-size: 35px;
      border-radius: 0;
      outline: none;
      border: none;
      padding-left: 80px;
      @media (max-width: 991px) {
        font-size: 30px;
        max-width: 100%;
        height: 60px !important;
        padding-left: 60px;
      }
      @media (max-width: 575px) {
        font-size: 25px;
        padding-left: 40px;
      }
      &::placeholder {
        color: black;
      }
    }
    &:before {
      content: url("images/location.svg");
      padding-right: 38px;
      position: absolute;
      top: 17px;
      left: 21px;
      @media (max-width: 991px) {
        padding-right: 23px;
        top: 10px;
        left: 10px;
      }
      @media (max-width: 575px) {
        padding-right: 8px;
      }
    }
    @media (max-width: 991px) {
      font-size: 30px;
      max-width: 100%;
      height: 60px;
    }
    @media (max-width: 575px) {
      font-size: 25px;
    }
  }
  &__check-in {
    margin-left: 5px;
    display: flex;
    border: none !important;
    position: relative;
    color: black;
    font-size: 25px;
    &__input {
      border: none;
    }
    @media (max-width: 991px) {
      margin-left: 0;
      font-size: 22px;
      margin-top: 5px;
    }
    @media (max-width: 575px) {
      margin-left: 0;
      font-size: 20px;
    }
    &:before {
      content: "";
      background-image: url("images/calendar.svg");
      width: 34px;
      height: 28px;
      overflow: visible;
      display: inline-block;
      background-size: cover;
      background-repeat: no-repeat;
      position: absolute;
      top: 23px;
      left: 7px;
      @media (max-width: 991px) {
        width: 30px;
        height: 25px;
        top: 19px;
      }
      @media (max-width: 575px) {
        width: 27px;
        height: 22px;
        top: 15px;
      }
    }
    &:after {
      content: "";
      background-image: url("images/triangle-check-in-2.svg");
      width: 46px;
      height: 75px;
      overflow: visible;
      display: inline-block;
      background-size: cover;
      background-repeat: no-repeat;
      margin-left: 0;
      @media (max-width: 991px) {
        width: 36px;
        height: 60px;
      }
      @media (max-width: 575px) {
        width: 30px;
        height: 50px;
      }
    }
    input {
      height: 75px;
      width: 155px;
      padding-left: 50px;
      color: black;
      outline: none;
      @media (max-width: 991px) {
        height: 60px;
        width: 145px;
        padding-left: 45px;
        font-size: 18px;
      }
      @media (max-width: 575px) {
        height: 50px;
        width: 135px;
        padding-left: 40px;
        font-size: 16px;
      }
      &::placeholder {
        color: black;
      }
    }
  }
  &__check-out {
    display: flex;
    border: none;
    margin-left: -35px;
    font-size: 25px;
    @media (max-width: 991px) {
      margin-left: -27px;
      font-size: 22px;
      margin-top: 5px;
    }
    @media (max-width: 575px) {
      margin-left: -20px;
      font-size: 20px;
    }
    &:before {
      content: "";
      background-image: url("images/shape-check-out-2.svg");
      width: 46px;
      height: 75px;
      overflow: visible;
      display: inline-block;
      background-size: cover;
      background-repeat: no-repeat;
      margin-right: -1px;
      @media (max-width: 991px) {
        width: 38px;
        height: 60px;
      }
      @media (max-width: 575px) {
        width: 30px;
        height: 50px;
      }
    }
    input {
      height: 75px;
      width: 155px;
      border: none;
      outline: none;
      padding-left: 20px;
      @media (max-width: 991px) {
        height: 60px;
        width: 130px;
        padding-left: 15px;
        font-size: 18px;
      }
      @media (max-width: 575px) {
        height: 50px;
        width: 110px;
        padding-left: 10px;
        font-size: 16px;
      }
      &::placeholder {
        color: black;
      }
    }
  }
  &__submit-button {
    button {
      height: 75px;
      width: 75px;
      margin-left: 5px;
      background-color: #faff00;
      border: 1px solid transparent;
      transition: 0.3s;
      @media (max-width: 991px) {
        height: 60px;
        width: 60px;
        margin-top: 5px;
      }
      @media (max-width: 575px) {
        height: 50px;
        width: 50px;
      }
      @media (max-width: 375px) {
        width: 283px;
        margin-left: 0;
      }
      @media (max-width: 310px) {
        width: 200px;
      }
      @media (max-height: 575px) {
        margin-bottom: 80px;
      }
      @media (max-height: 500px) {
        margin-bottom: 50px;
      }
      @media (max-height: 420px) {
        margin-bottom: 20px;
      }
      img {
        width: 100%;
        max-width: 27px;
        transition: 0.3s;
      }
      &:hover {
        border: 1px solid black;
        img {
          transform: scale(1.2, 1.2);
        }
      }
    }
  }
  &__submit-offer {
    button {
      height: 75px;
      width: 310px;
      margin-left: 5px;
      background-color: #faff00;
      border: 1px solid transparent;
      font-size: 35px;
      padding: 0 36px;
      transition: 0.3s;
      img {
        margin-top: -7px;
        padding-left: 15px;
        width: 100%;
        max-width: 42px;
        transition: 0.3s;
      }
      &:hover {
        border: 1px solid black;
        img {
          transform: scale(1.2, 1.2);
        }
      }
      @media (max-width: 991px) {
        height: 60px;
        margin-top: 10px;
        font-size: 30px;
        margin-left: 0;
        width: 270px;
        padding: 0 30px;
      }
      @media (max-width: 575px) {
        height: 50px;
        font-size: 25px;
        width: 235px;
        padding: 0 20px;
      }
      @media (max-width: 375px) {
        width: 283px;
        margin-left: 0;
      }
      @media (max-width: 310px) {
        width: 200px;
      }
      @media (max-height: 575px) {
        margin-bottom: 80px;
      }
      @media (max-height: 500px) {
        margin-bottom: 50px;
      }
      @media (max-height: 420px) {
        margin-bottom: 20px;
      }
    }
  }
}
.lightpick {
  font-family: $font-questrial, serif;
  border-radius: 0;
  margin-top: 5px;
  &__select {
    font-family: $font-questrial, sans-serif !important;
    font-weight: normal !important;
  }
  &__day-of-the-week {
    font-family: $font-questrial, sans-serif !important;
    font-weight: normal !important;
  }
  &__select-months {
    font-family: $font-questrial;
  }
  &__apply-action {
    background-color: #038597;
    border-radius: 0;
  }
  &__reset-action {
    border-radius: 0;
  }
  &__days {
    .is-start-date {
      background-image: none;
      background-color: transparent;
      border-radius: 50%;
      border: 1px solid #038597;
      color: black;
      &:hover {
        color: black !important;
        background-image: none;
      }
      .is-today {
        color: black !important;
      }
    }
  }
}



