.peaceful-place {
  padding: 90px 15px;
  @media (max-width: 991px) {
    padding: 80px 15px;
  }
  @media (max-width: 767px) {
    padding: 70px 15px;
  }
  @media (max-width: 575px) {
    padding: 60px 15px;
  }
  @media (max-width: 375px) {
    padding-top: 0;
  }
  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  h2 {
    color: #a20061;
    font-size: 80px;
    text-align: center;
    @media (max-width: 991px) {
      font-size: 64px;
    }
    @media (max-width: 767px) {
      font-size: 52px;
    }
    @media (max-width: 575px) {
      font-size: 45px;
    }
  }
  h3 {
    color: #939393;
    font-size: 40px;
    text-align: center;
    @media (max-width: 991px) {
      font-size: 35px;
    }
    @media (max-width: 767px) {
      font-size: 30px;
    }
    @media (max-width: 575px) {
      font-size: 25px;
    }
  }
}
.before-after {
  &__container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    max-height: 838px;
    position: relative;
    height: 100%;
    @media (max-width: 991px) {
      max-height: 575px;
    }
    @media (max-width: 767px) {
      max-height: 430px;
    }
    @media (max-width: 575px) {
      max-height: 350px;
    }
    @media (max-width: 375px) {
      max-height: 300px;
    }
    @media (max-width: 575px) {
      font-size: 27px;
    }
  }
  &__left {
    width: 50%;
    height: 100%;
    background-size: cover;
    background-position: top center;
  }
  &__right {
    width: 50%;
    height: 100%;
    background-size: cover;
    background-position: top center;
  }
}
.feedback {
  margin-top: 235px;
  margin-bottom: 170px;
  @media (max-width: 991px) {
    margin-top: 160px;
    margin-bottom: 130px;
  }
  @media (max-width: 767px) {
    margin-bottom: 80px;
  }
  @media (max-width: 575px) {
    margin-top: 55px;
    margin-bottom: 30px;
  }
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    @media (max-width: 991px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__left {
    padding-left: 120px;
    margin-right: 30px;
    @media (max-width: 991px) {
      padding: 0 80px;
    }
    @media (max-width: 575px) {
      padding: 0 30px;
    }
  }
  &__text {
    color: #2c2c2c;
    font-size: 50px;
    line-height: 52.75px;
    max-width: 600px;
    margin-bottom: 20px;
    position: relative;
    @media (max-width: 1200px) {
      font-size: 40px;
      line-height: 40px;
    }
    @media (max-width: 991px) {
      font-size: 35px;
      line-height: 35px;
    }
    @media (max-width: 767px) {
      font-size: 30px;
      line-height: 30px;
    }
    @media (max-width: 575px) {
      font-size: 27px;
      line-height: 27px;
    }
    &:before {
      content: url("images/brakes.svg");
      color: #faff00;
      width: 20px;
      height: 40px;
      position: absolute;
      left: -70px;
      top: -80px;
      z-index: -1;
      @media (max-width: 575px) {
        left: -30px;
        top: -40px;
      }
    }
  }
  &__author {
    color: #038697;
    font-size: 50px;
    line-height: 52.75px;
    @media (max-width: 1200px) {
      font-size: 40px;
      line-height: 40px;
    }
    @media (max-width: 991px) {
      font-size: 35px;
      line-height: 35px;
    }
    @media (max-width: 767px) {
      font-size: 30px;
      line-height: 30px;
    }
    @media (max-width: 575px) {
      font-size: 27px;
      line-height: 27px;
    }
    p {
      margin-bottom: 0;
    }
  }
  &__role {
    color: #b4b4b4;
    font-size: 30px;
    @media (max-width: 991px) {
      font-size: 27px;
    }
    @media (max-width: 767px) {
      font-size: 25px;
    }
    @media (max-width: 575px) {
      font-size: 22px;
    }
  }
  &__image {
    width: 455px;
    height: 455px;
    border-radius: 50%;
    background-size: cover;
    background-position: center;
    @media (max-width: 991px) {
      width: 380px;
      height: 380px;
    }
    @media (max-width: 767px) {
      width: 320px;
      height: 320px;
    }
    @media (max-width: 575px) {
      width: 250px;
      height: 250px;
    }
  }
}
.faq {
  background-color: #83004e;
  padding-top: 65px;
  padding-bottom: 100px;
  color: white;
  position: relative;
  overflow: hidden;
  h2 {
    color: white;
    font-size: 80px;
    margin-bottom: 60px;
    @media (max-width: 1200px) {
      margin-bottom: 0;
    }
    @media (max-width: 991px) {
      font-size: 65px;
    }
    @media (max-width: 767px) {
      font-size: 52px;
    }
    @media (max-width: 575px) {
      font-size: 45px;
    }
  }
  h3 {
    font-size: 40px;
    line-height: 42.2px;
    margin-bottom: 14px;
    @media (max-width: 991px) {
      font-size: 35px;
      line-height: 35px;
    }
    @media (max-width: 767px) {
      font-size: 30px;
      line-height: 30px;
    }
    @media (max-width: 575px) {
      font-size: 27px;
      line-height: 27px;
    }
  }
  p {
    color: #c0c0c0;
    font-size: 35px;
    @media (max-width: 991px) {
      font-size: 30px;
    }
    @media (max-width: 767px) {
      font-size: 27px;
    }
    @media (max-width: 575px) {
      font-size: 22px;
    }
  }
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    @media (max-width: 991px) {
      flex-direction: column;
    }
  }
  &__column {
    margin-left: 85px;
    &:first-child {
      margin-left: 0;
    }
    @media (max-width: 991px) {
      margin-left: 0;
    }
  }
  &__item {
    margin-top: 60px;
    @media (max-width: 991px) {
      margin-top: 50px;
    }
    @media (max-width: 767px) {
      margin-top: 45px;
    }
    @media (max-width: 575px) {
      margin-top: 40px;
    }
  }
  &__globe {
    position: relative;
    &:after {
      content: url("images/globe.svg");
      position: absolute;
      bottom: -90px;
      right: -400px;
    }
  }
}
.landlord-body {
  .landlord {
    padding-bottom: 30px;
    h2 {
      line-height: 105.5%;
      margin-bottom: 5px;
      @media (max-width: 1330px) {
        font-size: 90px;
        line-height: 100%;
      }
      @media (max-width: 1200px) {
        font-size: 60px;
      }
      @media (max-width: 991px) {
        font-size: 50px;
      }
    }
    h3 {
      @media (max-width: 1330px) {
        line-height: 100%;
      }
      @media (max-width: 1200px) {
        font-size: 35px;
        line-height: 106%;
        margin-top: 40px;
      }
      @media (max-width: 991px) {
        font-size: 30px;
        line-height: 106%;
        margin-top: 30px;
      }
    }
    p {
      font-size: 35px;
      line-height: 114%;
      @media (max-width: 1330px) {
        line-height: 100%;
      }
      @media (max-width: 1200px) {
        font-size: 30px;
        line-height: 114%;
      }
      @media (max-width: 991px) {
        font-size: 25px;
      }
    }
    &__right {
      background-repeat: no-repeat;
      background-color: white;
      background-position: right;
      background-size: auto 100%;
      @media (max-width: 767px) {
        background-position: top;
        background-size: contain;
        max-height: 100%;
        height: 700px;
      }
      @media (max-width: 575px) {
        height: 650px;
        //background-size: 100% auto;
      }
      @media (max-width: 410px) {
        height: 650px;
      }
    }
    &__content {
      @media (max-width: 767px) {
        margin-bottom: 70px;
      }
    }
  }
}



